::-webkit-input-placeholder { /* Edge */
  color: #B3B3B3;
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #B3B3B3;
}

::placeholder {
  color: #B3B3B3;
}

label[id$="error"] {
  @include absolute($B:-41px, $L:0);
  z-index: 10;
  max-width: 100%;
  background-color: #df5a16;
  border-radius: 3px;
  padding: 5px 10px;
  color: #fff;
  font-size: 14px;
  font-size: 0.875rem;

  &:before {
    @include psudoClass;
    @include absolute(-6px);
    @include triTop(#df5a16, 6px, 6px);
  }
}


